<template>
  <div>
    <div class="">
      <a-button class="editable-add-btn" @click="addItemAuto">
        Add
      </a-button>
      <a-button type="danger" class="editable-add-btn" @click="handleClear">
        Clear
      </a-button>
    </div>

    <a-table
        :bordered="true"
        :data-source="dataSource"
        :columns="columns"
        :scroll="{ x: 'calc(900px + 50%)'}"
    >
      <template slot="title" slot-scope="text, record">
        <editable-cell :text="text" @change="onCellChange(record.key, 'title', $event)"/>
      </template>
      <template slot="subtitle" slot-scope="text, record">
        <editable-cell :text="text" @change="onCellChange(record.key, 'subtitle', $event)"/>
      </template>
      <template slot="url" slot-scope="text, record">
        <editable-cell :text="text" @change="onCellChange(record.key, 'url', $event)"/>
      </template>
      <template slot="remark" slot-scope="text, record">
        <editable-cell :text="text" @change="onCellChange(record.key, 'remark', $event)"/>
      </template>
      <template slot="operation" slot-scope="text, record">
        <a-popconfirm
            v-if="dataSource.length"
            title="Sure to delete?"
            @confirm="() => onDelete(record.key)"
        >
          <a href="javascript:;">Delete</a>
        </a-popconfirm>
      </template>
    </a-table>
  </div>
</template>

<script>
import EditableCell from "@/components/list/editableCell";
import {Button, Table, Popconfirm} from 'ant-design-vue'
import { createNamespacedHelpers } from 'vuex'
const { mapMutations } = createNamespacedHelpers('luckCardModule')
import Vue from 'vue'

Vue.use(Table)
Vue.use(Button)
Vue.use(Popconfirm)

export default {
  name: 'item-list',
  props: {
/*    dataSource: {
      type: Array,
      // default: function () { return [] }
      default: () => []
    }*/
  },
  components: {
    EditableCell
  },
  data() {
    return {
      list: this.dataSource,
      count: 2,
      columns: [
        {
          title: 'id',
          dataIndex: 'key',
        },
        {
          title: 'title',
          dataIndex: 'title',
          width: '20%',
          scopedSlots: {customRender: 'title'},
        },
        {
          title: 'subtitle',
          dataIndex: 'subtitle',
          width: '20%',
          scopedSlots: {customRender: 'subtitle'},
        },
        {
          title: 'url',
          dataIndex: 'url',
          width: '30%',
          scopedSlots: {customRender: 'url'},
        },
        {
          title: 'remark',
          dataIndex: 'remark',
          scopedSlots: {customRender: 'remark'},
        },
        {
          title: 'operation',
          dataIndex: 'operation',
          width: '100px',
          fixed: 'right',
          scopedSlots: {customRender: 'operation'},
        },
      ],
    };
  },
  methods: {
    onCellChange(key, dataIndex, value) {
      const dataSource = [...this.dataSource];
      const target = dataSource.find(item => item.key === key);
      if (target) {
        target[dataIndex] = value;
        this.dataSource = dataSource;
      }
    },
    onDelete(key) {
      this.delItemByKey(key)
    },
    handleClear () {
      this.$confirm({
        title: 'Are you sure clear this list?',
        content: 'This operation is not recoverable！',
        okText: 'Yes',
        okType: 'danger',
        cancelText: 'No',
        onOk: () => {
          this.cleatAll()
        },
        onCancel () {
          console.log('Cancel')
        },
      })
    },
    ...mapMutations(['addItemAuto']),
    ...mapMutations(['delItemByKey']),
    ...mapMutations(['cleatAll'])
  },
  computed: {
    dataSource() {
      return this.$store.getters['luckCardModule/getDataSource']
    }
  }
};
</script>

<style>

.ant-table-title {
  visibility: hidden;
  height: 0;
}

.editable-add-btn {
  /*width: 100px;*/
  margin-right: 10px;
}

</style>
